$MAX_WIDTH: 568px;
$MIN_WIDTH: 375px;

$BGC_HEADER: cadetblue;
$BGC_GREEN_HOVER: #5f9ea093;
$BGC_MAIN: #f3f3f3;
$BGC_GRAY: #e4e4e4;
$BGC_CATEGORY_ITEM: red;
$BGC_DELETED_ITEM: #ff000047;

$BORDER_INPUT_COLOR: #8080806b;

$POPUP_TRANSITION: 500ms;

@function convertToVw($fontSizePx) {
    @return ($fontSizePx / 375) * 100 + vw;
}

@function convertToPx($size) {
    @return $size / 375 * 568 + px;
}

@mixin container {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: convertToVw(375);
    margin: 0 auto;
    @media screen and (min-width: $MAX_WIDTH) {
        width: convertToPx(375);
    }
}

@mixin iconHoverColor($color) {
    &:hover {
        fill: $color;
    }
}
